.button {
    width: 132px;
    height: 30px;
    border-radius: 3px;
    color: white;
    cursor: pointer;
}

.disabled {
    background-color: #004D99;
    color: #002E5C;
}

.secondary {
    border-color: #0066CC;
    background-color: white;
    color: #0066CC;
}

.default {
    background-color: #0066CC;
    border: none;
}

.red {
    background-color: #CC1439;
    border: none;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
    color: #0066CC;
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
    color: #0066CC;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
    background-color: #004D99;
    color: #002E5C;
    cursor: not-allowed;
    opacity: 0.5;
}
