}

.nav {
}

.nav a {
    font-family: Montserrat, "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: 600;
    font-size: 22px;
    text-decoration: none;
}

.nav a:hover {
    text-decoration: none;
    color: #58ACFF;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}
